$component: --mt-titulo-h3;

:host {
  #{$component}-text-color: var(--mt-color-titulo-h3-fonte);
  #{$component}-text-font-family: 'Roboto', sans-serif;
  #{$component}-text-font-weight: 900;
  #{$component}-text-text-transform: uppercase;
  #{$component}-text-font-size: 14px;

  #{$component}-line-color: var(--mt-color-titulo-h3-linha);
  #{$component}-line-width: 100%;
  #{$component}-line-height: 4px;
  #{$component}-line-margin: 0 0 2px 0;

  display: block;
}

.mt-titulo-h3 {
  &__container {
    display: flex;
    flex-direction: column;
  }

  &__line {
    margin: var(#{$component}-line-margin);
    background-color: var(#{$component}-line-color);
    width: var(#{$component}-line-width);
    height: var(#{$component}-line-height);
  }

  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;
  }
}
